@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;400;500;600&family=Mulish&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-size: 1.4rem;
}

.container {
  background: linear-gradient(to right bottom, #e1b382, #c89666);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  background-color: #12343b;
  min-width: 250px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
}

.header {
  color: #c89666;
  text-align: center;
  margin: 30px 0;
}

/*Form */
.task-input {
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #000000;
  border: 1px solid #c89666;
  border-radius: 10px;
}

.button-add {
  width: 70px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #f1af71;
  cursor: pointer;
}

/*Todo-List*/
.list-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid #f1af71;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: lightseagreen;
  margin-right: 10px;
}

.button-delete {
  color: #ff6c6c;
}

.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ff6c6c;
  opacity: 0.6;
}

.list {
  width: 250px;
  background-color: #12343b;
  border: none;
  color: #f1af71;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
  text-overflow: ellipsis;
  overflow: auto;
}

input:focus,
button:focus {
  outline: none;
}

/*Media Query*/

/*Small*/
@media screen and (max-width: 480px) {
  .button-add {
    width: 70px;
    padding: 10px 10px;
    font-size: 20px;
    border-radius: 10px;
    border: 0;
    background-color: #f1af71;
    cursor: pointer;
  }

  .complete-delete {
    display: flex;
  }

  .app-wrapper {
    background-color: #12343b;
    width: 340px;
    min-height: 450px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 40px #000;
  }

  .form {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .task-input {
    outline: none;
    width: 160px;
    padding: 15px;
    margin-right: 25px;
    font-size: 20px;
    color: #ccc;
    background-color: #000000;
    border: 1px solid #c89666;
    border-radius: 10px;
  }
}
/*Smaller*/
@media screen and (max-width: 360px) {
  .button-add {
    width: 60px;
    padding: 10px 10px;
    font-size: 15px;
    border-radius: 10px;
    border: 0;
    background-color: #f1af71;
    cursor: pointer;
  }

  .complete-delete {
    display: flex;
  }

  .app-wrapper {
    background-color: #12343b;
    width: 290px;
    min-height: 450px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 40px #000;
  }

  .form {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .task-input {
    outline: none;
    width: 128px;
    padding: 10px;
    margin-right: 25px;
    font-size: 15px;
    color: #ccc;
    background-color: #000000;
    border: 1px solid #c89666;
    border-radius: 10px;
  }

  .list-item {
    display: flex;
    margin: 20px 0;
    border: 1px solid #f1af71;
    border-radius: 10px;
    padding: 5px;
    max-height: 30px;
  }

  .list {
    width: 250px;
    background-color: #12343b;
    border: none;
    color: #f1af71;
    font-size: 15px;
    padding-left: 10px;
    margin-right: 15px;
    text-overflow: ellipsis;
    overflow: auto;
  }
}
